html {
  height: 100%;
}
body {
  font-family: 'OpenSans', 'Arial', sans-serif;
  line-height: 22px;
  font-size: 14px;
  color: #333;
  font-weight: 400;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
img {
  width: 100%;
  height: 100%;
  max-width: 100%;
  object-fit: contain;
}
li {
  list-style: none;
}
@media (max-width: 1280px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 auto;
  }
}
.header {
  background: linear-gradient(to right, #FFF, #FF9900);
  height: 45px;
  padding-left: 15px;
  display: flex;
  align-items: center;
}
.card {
  margin-top: 20px;
}
.card__wrapper {
  margin-right: 10px;
  margin-bottom: 20px;
  padding: 20px;
}
.card__wrapper.client {
  border: none;
  display: flex;
  gap: 10px;
}
.client__hover {
  filter: opacity(0.5);
  text-decoration: none;
  color: #333;
}
.client__hover.client__hover--js {
  filter: opacity(1);
}
.client__list {
  display: none;
}
.client__list.client__list--js {
  display: block;
  transition: 0.3s ease;
  filter: opacity(1);
}
.client {
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  width: 30%;
  padding: 10px;
  overflow: auto;
}
.card__wrapper {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.client__button {
  border: 1px solid #FF9900;
  cursor: pointer;
  background-color: transparent;
  font-weight: 700;
  padding: 5px;
  width: 100px;
  margin: 0 auto;
}
.footer__text {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 0;
  height: 100%;
}
.footer {
  height: 45px;
  background: linear-gradient(to right, #FFF, #FFCC66);
}
